export const useTotalValue = (): BigNumber => {

  const farms = useFarms()
  console.log("DebugFarm: ", farms)
  const ethUsdtFarm = farms.data.find((farm: Farm) => farm.pid === 1)
  // console.log("DebugEFarm: ", ethUsdtFarm )
  const ethPriceBusd = ethUsdtFarm.tokenPriceVsQuote ? BIG_ONE.div(ethUsdtFarm.tokenPriceVsQuote) : BIG_ZERO
  const ethPrice = ethPriceBusd
  const pepePrice = usePriceCakeBusd()
  const stakingLPtokenPrice = useLpTokenPrice("PEPE-ETH LP")
  let value = new BigNumber(0)
  const { pools } = usePools(false)
  const WDEFI_POOL_PID = 9  // WDEFI-BUSD
  for (let i = 0; i < pools.length; i++) {
    const pool = pools[i]
    // console.log('debugTVL', pools, pepePrice.toNumber(), ethPrice.toNumber(), stakingLPtokenPrice.toNumber(), pool.totalStaked.toNumber())
    if (pool.totalStaked) {
      console.log("debugHere")
      let val
      if (pool.stakingToken.symbol === 'PEPE') {
        val = pepePrice.times(pool.totalStaked).div(new BigNumber(10).pow(18))
      } else if (pool.stakingToken.symbol === 'wETH') {
        val = ethPrice.times(pool.totalStaked).div(new BigNumber(10).pow(18))
      } else {
        val = stakingLPtokenPrice.times(pool.totalStaked).div(new BigNumber(10).pow(18))
      }
      value = value.plus(val)
    }
  }
  console.log("dubugPPP: ", value.toNumber())
  return value
}